
.resume-cover{
/*	background: red;*/
	font-family: arial;

}
.resume-cover h1{
	font-family: 'Poppins',sans-serif;
	text-align: center;
	font-weight: bold;
	padding: 1vw 0px;
	font-size: 3vw;
	margin: 1.5vw;
}

.resume-cover h2{
	font-family: 'Poppins',sans-serif;
	font-weight: bold;
	font-size: 2.7vw;
	margin: 0px;
	color: black;
}

.resume-cover h3{
	font-weight: 700;
	font-size: 1.8vw;
	margin: 0px;
	color: black;
	margin-top: 1.9vw;
}
.resume-cover h6{
	margin: 0px;
	font-weight: bold;
	color: black;
	font-size: 1.5vw;
/*	padding-left: 15px;*/
	margin-top: .2vw;

}
.resume-cover table{
	margin-top: 5px;
}
.resume-cover table tr td{
/*	background-color: yellow;*/
padding-right: .5vw;
color: black;
font-size: 1.4vw;
}

.resume-cover p{
	font-size: 1.4vw;
	text-align: justify;
	margin: 0px;
	color: black;
	padding-top: .2vw;
}
.resume-cover ul{
	margin: 0px;
	padding: 0px;
	padding-left: 2vw;
}
.resume-cover ul li{
	font-size: 1.4vw;
	margin: 0px;
	color: black;
	padding: .2vw;
}
.resume-cover span{
	font-weight: bold;
	font-size: 1.4vw;
	color: black;
}



/*media query*********************************************************************/
/*extra small*/
@media screen and (max-width:576px){

  .resume-cover h1{
	padding: 1vw 0px;
	font-size: 7vw;
	margin: 2.5vw;
	}

	.resume-cover h2{
		font-size: 5vw;
		margin: 0px;
	}

	.resume-cover h3{
		font-size: 3.01vw;
		
		margin-top: 2vw;
	}
	.resume-cover h6{
		font-size: 2vw;
		margin-top: .2vw;
		margin-bottom: .2vw;
		
	}
	.resume-cover table{
		margin-top: .2ww;
	}
	.resume-cover table tr td{
	
	padding-right: .5vw;
	font-size: 2vw;
	}

	.resume-cover p{
		font-size: 2vw;
		
		padding-top: .2vw;
	}
	.resume-cover ul{
		
		padding-left: 2vw;
	}
	.resume-cover ul li{
		font-size: 2vw;
		
		padding: .2vw!important;
	}
	.resume-cover span{
		font-size: 2vw;
		
	}
  
}


/*small*/
@media screen and (min-width:577px) and (max-width:767px){
	.resume-cover h1{
	padding: 1vw 0px;
	font-size: 5vw;
	margin: 2.5vw;
	}

	.resume-cover h2{
		font-size: 4vw;
		margin: 0px;
	}

	.resume-cover h3{
		font-size: 2.2vw;
		
		margin-top: 2vw;
	}
	.resume-cover h6{
		font-size: 1.7vw;
		margin-top: .2vw;
		margin-bottom: .2vw;
	}
	.resume-cover table{
		margin-top: .2ww;
	}
	.resume-cover table tr td{
	
	padding-right: .5vw;
	font-size: 1.6vw;
	}

	.resume-cover p{
		font-size: 1.6vw;
		
		padding-top: .2vw;
	}
	.resume-cover ul{
		
		padding-left: 2vw;
	}
	.resume-cover ul li{
		font-size: 1.6vw;
		
		padding: .2vw!important;
	}
	.resume-cover span{
		font-size: 1.6vw;
		
	}
   
}
